.toggle {
  border: 0;
  padding: 0;
  outline: none;
  cursor: pointer;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 50px;
  background: 0 0; }
  @media (min-width: 660px) {
    .toggle {
      margin-top: 15px; } }
  @media (min-width: 960px) {
    .toggle {
      display: none; } }

.toggle:hover .toggle__icon--burger,
.toggle:hover span::before,
.toggle:hover span::after {
  background: #d22856;
  transition: all 0.3s 0s; }

.toggle:active .toggle__icon--burger:active span::before,
.toggle:active .toggle__icon--burger:active span::after {
  background: rgba(210, 40, 86, 0.3); }

.toggle__icon {
  position: relative;
  width: 50px;
  height: 4px;
  background-color: white;
  transition: background-image 0.3s 0s; }
  .toggle__icon::before, .toggle__icon::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background: white;
    transform-origin: 50% 50%;
    transition: all 0.3s 0s, transform 0.3s; }
  .toggle__icon--burger::before {
    top: 10px; }
  .toggle__icon--burger::after {
    top: -10px; }
  .toggle__icon--cross {
    background: 0 0;
    width: 30px; }
    .toggle__icon--cross::before {
      top: 0;
      transition: all 0.3s, transform 0.3s 0.5s;
      transform: rotate3d(0, 0, 1, 45deg); }
    .toggle__icon--cross::after {
      top: 0;
      transition: all 0.3s, transform 0.3s 0.5s;
      transform: rotate3d(0, 0, 1, -45deg); }

