.social-buttons {
  padding-top: 38px;
  display: flex;
  padding-left: 12px;

  @media #{$tablet-widths} {
    padding-top: 5px;
    padding-right: 3px;
    padding-left: 0;
  }
}

.social-buttons__item {
  width: 46px;
  height: 46px;
  border: solid 3px $blue-10;
  border-radius: 50%;
  margin-right: 13px;

  &:focus {
    border-color: $pink;
  }

  &:hover {
    border-color: $blue;
  }

  &:active {
    opacity: 0.3;
    border-color: $blue-30;
  }

  &--twitter {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    fill: $pink;
  }

  &--fb {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    fill: $pink;
  }

  &--youtube {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    fill: $pink;
  }
}
