.photo-like {
  text-decoration: none;
  margin-right: 19px;

  @media #{$tablet-widths} {
    order: 2;
    margin-right: 4px;
    margin-left: 0;
  }

  &:focus {
    box-shadow: 0 0 0 0.1em $pink;
    border-radius: 0.2em;
  }
}

.photo-like__icon {
  fill: $pink;

  &:hover {
    fill: $blue;
  }

  &:active {
    fill: $blue-30;
  }
}
